.information

  &__el
    margin-top: 10px
    margin-bottom: 15px

  &__title
    display: block
    margin-bottom: 7px
    letter-spacing: -.3px
    line-height: 1.4
    font-size: 18px
    +semibold

    &.is-fz-14
      font-size: 14px

  &__text
    display: inline-block

  &__descr
    margin-top: 22px
    margin-bottom: 20px
    +m(480)
      margin-top: 10px
      margin-bottom: 15px

  &__link
    color: $text
    &:hover
      text-decoration: underline

  .list
    margin-top: 6px
    margin-bottom: 23px

    &__el
      margin-bottom: 2px
